export default {
    name: "Nav",
    components: {},
    props: {
        navList: {
            type: Array,
            default: []
        }
    },

    data() {
        return {
            selectItem: 0
        };
    },

    methods: {
        to(item) {
            this.selectItem = item.id;
            this.$emit("nav", item);
        }
    }
};



