
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
name: "InputBox",
components: { XInput, XTextarea },

data() {
return {
inputText: ""
};
},

methods: {
send() {
this.$emit("send", this.inputText);
this.inputText = "";
}
}
};



