
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
name: "Header",
components: { PopupRadio, PopupPicker },

data() {
return {
genderOptions: [["男", "女"]],
gender: ["男"],
ageOptions: [[]],
age: ["22"]
};
},

methods: {
getAgeOptions() {
let ageOptions = [];
for (let i = 1; i <= 100; i++) {
ageOptions.push(`${i}`);
}
this.ageOptions = [ageOptions]
},
selectChange(){
let params = {
age: this.age[0],
gender: this.gender[0]
}
this.$emit('selectChange',params)
}
},
created() {
this.getAgeOptions();
}
};



