import Header from "../components/Header";
import InputBox from "../components/InputBox";
import Nav from "../components/Nav";
import { autoResponse } from "../lib/autoResponse";
import {
    queryCheckBodyList,
    querySymptomList,
    queryQuestionOptionList,
    queryType,
    queryDiseaseDetail
} from "../service/index";
export default {
    name: "Main",
    components: { Header, InputBox, Nav },

    data() {
        return {
            navList: [
                {
                    icon: "icon-zhineng",
                    type: "智能导诊",
                    path: "/guidance",
                    id: 0
                },
                {
                    icon: "icon-duihua",
                    type: "智能问病",
                    path: "/askingDisease",
                    id: 1
                },
                {
                    icon: "icon-yao",
                    type: "药物查询",
                    path: "drugInquiry",
                    id: 2
                },
                {
                    icon: "icon-fankui",
                    type: "意见反馈",
                    path: "/feedback",
                    id: 3
                }
            ],
            arr: [],
            navItem: {},
            age: 24,
            gender: "男",
            pageCount: 1
        };
    },

    methods: {
        // 年龄性别变更
        ageChange(params) {
            this.age = params.age;
            this.gender = params.gender;
        },
        // 头部导航
        nav(item) {
            this.navItem = item;
            if (item.type != "意见反馈") {
                this.arr = [];
                this.$router.push(item.path);
                this._response(item, "");
            } else {
                this.$router.push("/feedback");
            }
        },
        // 发送
        send(inputText) {
            if (inputText != "") {
                this.arr.push({
                    type: "C",
                    icon: "icon-tubiao_guke",
                    content: inputText
                });
                this._getQueryType(inputText);
                // this._response("", inputText);
                // let ele = document.getElementsByClassName("bubble-box");
                // ele.scrollTop = ele.scrollHeight;
            }
        },
        // 自动应答
        _response(item, inputText) {
            let obj = autoResponse(item, inputText);
            this.arr.push({
                source: "U",
                icon: "icon-customer",
                content: obj
            });
        },
        // 点击聊天，跳转
        learnMore(i) {
            // this._response("", i);
            this.$router.push(i.path);
        },
        // 询问类型
        _getQueryType(inputText) {
            queryType({ inputContent: inputText }).then(res => {
                if (res.data.type == 1) {
                    this.arr.push(
                        Object.assign(res.data, {
                            source: "U",
                            icon: "icon-customer",
                            tip: "您是否查找以下疾病？",
                            description: "diseaseList"
                        })
                    );
                } else if (res.data.type == 2) {
                    this.arr.push(
                        Object.assign(res.data, {
                            source: "U",
                            icon: "icon-customer",
                            tip: "您是否查找以下药物？"
                        })
                    );
                }
                // else if (res.data.type == 3) {
                //     this.arr.push(
                //         Object.assign(rea.data, {
                //             source: "U",
                //             icon: "icon-customer",
                //             tip: "您是否患有以下症状？"
                //         })
                //     );}
                else {
                    this._getBodyList(this.pageCount).then(res => {
                        this.arr.push(
                            Object.assign(res.data, {
                                source: "U",
                                icon: "icon-customer",
                                tip: "您身体哪个部位不舒服?",
                                description: "bodyList"
                            })
                        );
                    });
                }
            });
        },
        _getDiseaseDetail(diseaseId) {
            queryDiseaseDetail({ diseaseId: diseaseId }).then(res => {
                this.arr.push(
                    Object.assign(res.data, {
                        source: "U",
                        icon: "icon-customer",
                        description: "diseaseDetail",
                        type: 1
                    })
                );
            });
        },
        _getBodyList(pageCount) {
            let params = {
                pageNum: pageCount,
                pageSize: 10
            };
            return new Promise((resolve, reject) => {
                queryCheckBodyList(params).then(res => {
                    resolve(res);
                });
            });
        },
        nextPage(pageCount) {
            this._getBodyList(pageCount).then(res => {
                this.arr.push(
                    Object.assign(res.data, {
                        source: "U",
                        icon: "icon-customer",
                        tip: "您身体哪个部位不舒服?",
                        description: "bodyList"
                    })
                );
            });
        },
        _getSymptomList(id) {
            let params = {
                pageNum: this.pageCount,
                pageSize: 10,
                age: this.age,
                gender: this.gender == "男" ? "M" : "F",
                bodyId: id
            };
            querySymptomList(params).then(res => {
                this.arr.push(
                    Object.assign(res.data, {
                        source: "U",
                        icon: "icon-customer",
                        tip: "您是否患有以下症状？",
                        description: "SymptomList"
                    })
                );
            });
        }
    },
    created() {
        this.nav(this.navList[0]);
        // this._getBodyList();
        // this._getSymptomList();
    }
};



